import React, { Component } from 'react';
import TodoItem from '../components/TodoItem';
import todosData from '../data/todosData';

class MainContent extends Component {
    constructor(){
        super();
        this.state = {
            todos: todosData,
        };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(id){
        this.setState(prevState => {
            // get previous state
            const previousState = Object.assign({}, prevState);

            // process state (update previous state)
            const updatedTodos = previousState.todos.map(todo => {
                if (todo.id === id){
                    todo.completed = !todo.completed;
                }
                return todo;
            });

            // update current state
            return {
                todos: updatedTodos
            }            
        });

        
    }

    render(){
        // Time of day!
        const styles = {
            fontSize: 30,
            margin: "10px"
          }
        const timeOfDay = getTimeOfDay(styles);

        // TODO App
        const todoItems = this.state.todos && this.state.todos.map(
            (item) => (<TodoItem key={item.id} item={item} handleChange={this.handleChange} />)
        );
        return (
            <main className="main">
                <h1 style={styles}>Good {timeOfDay}!</h1>
                <p>This is where most of my content will go...</p> 
                <div className="todo-list"> 
                    {todoItems}
                </div>         
            </main>
        );
    }
}

export default MainContent;

function getTimeOfDay(styles) {
    const date = new Date()
    const hours = date.getHours()

    let timeOfDay

    if (hours < 12) {
        timeOfDay = "morning"
        styles.color = "#04756F"
    }
    else if (hours >= 12 && hours < 17) {
        timeOfDay = "afternoon"
        styles.color = "#2E0927"
    }
    else {
        timeOfDay = "night"
        styles.color = "#D90000"
    }

    return timeOfDay;
}