import { connect } from "react-redux";
import React from "react";

// API
const API = {
  GET_RANDOM_DOG: "https://dog.ceo/api/breeds/image/random"
};

// Action Types
const types = {
  REQUESTED_DOG: "REQUESTED_DOG",
  REQUESTED_DOG_SUCCEEDED: "REQUESTED_DOG_SUCCEEDED",
  REQUESTED_DOG_FAILED: "REQUESTED_DOG_FAILED"
};

// initial state
const initialState = {
  url: "",
  loading: false,
  error: false
};

// reducer
export const reduxReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.REQUESTED_DOG:
      return {
        ...initialState,
        loading: true
      };
    case types.REQUESTED_DOG_SUCCEEDED:
      return {
        ...initialState,
        url: action.url,
      };
    case types.REQUESTED_DOG_FAILED:
      return {
        ...initialState,
        error: true
      };
    default:
      return state;
  }
};

// Action Creators
const requestDog = () => {
  return { type: types.REQUESTED_DOG };
};

const requestDogSuccess = data => {
  return { type: types.REQUESTED_DOG_SUCCEEDED, url: data.message };
};

const requestDogError = () => {
  return { type: types.REQUESTED_DOG_FAILED };
};

const fetchDog = async dispatch => {
  dispatch(requestDog());
  try {
    const res = await fetch(API.GET_RANDOM_DOG);
    const data = await res.json();
    return dispatch(requestDogSuccess(data));
  } catch (err) {
    return dispatch(requestDogError());
  }
};

// Component
class App extends React.Component {
  render() {
    return (
      <div>
        <button onClick={() => fetchDog(this.props.dispatch)}>Show Dog</button>
        <br />
        {this.props.loading ? (
          <span style={{ minWidth: "500px", background: "grey" }}>
            Loading...
          </span>
        ) : this.props.error ? (
          <p>Error, try again</p>
        ) : (
          <span>
            <img style={{ maxWidth: "500px" }} src={this.props.url} />
          </span>
        )}
      </div>
    );
  }
}

// connectedapps
export const ReduxApp = connect(state => {
  console.log(state);
  return state;
})(App);
