import React, { Component } from "react";
import Tracklist from './components/Tracklist'
import './index.css';

const TRACKLISTS = 'https://www.1001tracklists.com/ajax/search_tracklist.php?noIDFieldCheck=true&fixedMode=true';
const SEARCHPARAMETER = '&sf=term';
const TERM = '&term=';
const MAINIMAGE = 'https://phaseflow.github.io/images/logo16T2560.png';

class App extends Component {
  constructor() {
    super();
    this.state = {
      character: {},
      // searchTerm: "",
      loading: false,
      tracklists: [],
      innerHtml: "",
    };

    this.handleChange = this.handleChange.bind(this)
  }

  handleChange(event) {
    const searchTerm = event.target.value;
    if (searchTerm.length > 2) {
      this.setState({
        searchPhrase: event.target.value,
        loading: true,
      });
      fetch(`${TRACKLISTS}${SEARCHPARAMETER}${TERM}${searchTerm}`)
      .then(response => response.json())
      .then(data => {
        console.log(data.data);
        this.setState({
          tracklists: data.data,
          loading: false,
        });
      })
      .catch((e)=> {
        console.log('Error with request: ' + e);
        this.setState({
          tracklists: [{"informal":"Error with request"}],
          loading: false,
        });
      });
    }
    else {
      this.setState({
        searchPhrase: "",
        loading: false,
        tracklists: [],
      })
    }
    
}

  componentDidMount() {    
    console.log("Component did mount! Processing APIs...");
    //this.setState({loading: true});
    // fetch(`${SW_API}people/10/`)
    //   .then(response => response.json())
    //   .then(data => {
    //     console.log(data);
    //     this.setState({
    //       character: data,
    //       loading: false,
    //     })
    //   });
  }

  render() {
    console.log('Component is rendering...')
    const text = this.state.loading ? 'Loading...' : (this.state.tracklists.length > 0 && 'Done!');
    return (
      <>
        <div className="part10c">
          <img className="pic" src={MAINIMAGE} alt="imgtext" />
          <h3>{text}</h3>
        </div>
        <div className="form">
          <form>
                <input type="text" placeholder="Search tracklists..." onChange={this.handleChange} className="searchInput" />
                <h2>{!!this.state.searchPhrase && !this.state.loading && `Results of '${this.state.searchPhrase}':`}</h2>
          </form>
        </div>
        <div>
          {!this.state.loading && this.state.tracklists && 
            this.state.tracklists.map((tracklist, index ) => {
              return (
                <Tracklist key={index} tracklist={tracklist} />
              )
            })
          }
        </div>
        {/* <div dangerouslySetInnerHTML={this.state.innerHtml} /> */}
      </>
    );
  }
}




export default App