import { connect } from "react-redux";
import React from "react";

import { call, put, takeEvery } from 'redux-saga/effects';

// API
const API = {
  GET_RANDOM_DOG: "https://dog.ceo/api/breeds/image/random"
};

// Action Types
const types = {
  REQUESTED_DOG: "REQUESTED_DOG",
  REQUESTED_DOG_SUCCEEDED: "REQUESTED_DOG_SUCCEEDED",
  REQUESTED_DOG_FAILED: "REQUESTED_DOG_FAILED",
  FETCHED_DOG: "FETCHED_DOG"
};

// initial state
const initialState = {
  url: "",
  loading: false,
  error: false
};

// reducer
export const sagaReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.REQUESTED_DOG:
      return {
        ...initialState,
        loading: true
      };
    case types.REQUESTED_DOG_SUCCEEDED:
      return {
        ...initialState,
        url: action.url
      };
    case types.REQUESTED_DOG_FAILED:
      return {
        ...initialState,
        error: true
      };
    default:
      return state;
  }
};

// action creators
const requestDog = () => {
  return { type: types.REQUESTED_DOG };
};

const requestDogSuccess = data => {
  return { type: types.REQUESTED_DOG_SUCCEEDED, url: data.message };
};

const requestDogError = () => {
  return { type: types.REQUESTED_DOG_FAILED };
};

const fetchDog = () => {
  return { type: types.FETCHED_DOG };
};

// sagas
export function* watchFetchDog() {
  yield takeEvery(types.FETCHED_DOG, fetchDogAsync);
}

function* fetchDogAsync() {
  try {
    // declare requestDog action: state is 'loading'
    yield put(requestDog());
    // declare call to API
    const data = yield call(() => {
      return fetch(API.GET_RANDOM_DOG).then(res => res.json());
    });
    // declare requestDogSuccess action: state is 'success'
    yield put(requestDogSuccess(data));
  } catch (error) {
    // declare requestDogError action: state is 'error'
    yield put(requestDogError());
  }
}

// component
class App extends React.Component {
  render() {
    return (
      <div>
        <button onClick={() => this.props.dispatch(fetchDog())}>
          Show Dog
        </button>
        <br />
        {this.props.loading ? (
          <span style={{ minWidth: "500px", background: "cyan" }}>
            Loading...
          </span>
        ) : this.props.error ? (
          <p>Error, try again</p>
        ) : (
          <span>
            <img style={{ maxWidth: "500px" }} src={this.props.url} />
          </span>
        )}
      </div>
    );
  }
}

export const SagaApp = connect(state => {
  console.log(state);
  return state;
})(App);