import React from 'react';
// import TracklistData from './TracklistData';

const Tracklist = ({ tracklist }) => {
    const properties = tracklist.properties;
    const informal = tracklist.informal;

    if (!!tracklist.informal || !properties) {
        return (
            <div className="tracklistEntity">
                <p class="tracklistEntityContent">{tracklist.informal}</p>
            </div>
        )
    }

    const tracklistUrlPrefix = "https://1001.tl/";

    const name = properties.tracklistname; // str
    const id = properties.id_tracklist; // str
    const uid = properties.id_unique; // str, this one used to create valid request
    const urlName = properties.url_name; // str
    const isLive = properties.is_live; // bool

    const delimiter = ' – ';
    const nameParts = parseName(name);

    const tracklistFullUrl = `${tracklistUrlPrefix}${uid}`;

    return (
        <>
            <a href={tracklistFullUrl} target="_blank" className="tracklist">
                <div className="tracklistEntity">
                    <p className="tracklistEntityContent">
                        {!!nameParts[0] && <><strong className="author">{(nameParts[0])}</strong>{delimiter}</>}
                        {nameParts[1]}
                        <br />                        
                        {/* <TracklistData tracklistFullUrl={tracklistFullUrl}/> */}
                    </p>
                </div>                    
            </a>                    
        </>
    )
}

const parseName = (name) => {
    try {
        if (name.includes(" @ ")) {
            return name.split(" @ ");
        }

        if (name.includes(" - ")) {
            return name.split(" - ");
        }
    } catch (error) {
        console.log("Something went wrong!" + error)
    }
    return ["", name];
}

export default Tracklist